import React from 'react';
import Layout from '../../components/Layout/Layout';
import Progects from './Projects';

const title = 'projects';
async function action() {
  return {
    chunks: ['projects'],
    title,
    component: (
      <Layout Header="Projects">
        <Progects />
      </Layout>
    ),
  };
}

export default action;



