import React from 'react';
import s from './ProgectWrapper.css';
import withStyles from 'isomorphic-style-loader/lib/withStyles';

class ProjectWrapper extends React.Component {
  render() {
    return (
         <a
            target="_blank"
            href={this.props.data.link}
            className={s.WrapperProject} >
              <div className={s.frontFilter}></div>
              <img src={this.props.data.Img} />
              <h2 className={s.headerProgect}>{this.props.data.header}</h2>
              <span className={s.descProgect}>{this.props.data.shortDescription}</span>
         </a>
    );
  }
}

export default withStyles(s)(ProjectWrapper);



